console.log('****************====>'+process.env.ENV_TYPE);//dev test pro
console.log('url=====>'+process.env.VUE_APP_BASE_URL);
const baseURL = process.env.VUE_APP_BASE_URL;//todos?userId=1







console.log("    "+process.env.NODE_ENV)
const timeout = 10000;
// api
const request = function(option){ 
	// url
	var url = baseURL + option.url; 
	 
	// 
	var  header = option.header||{}
	// if(!!option.needToken){
	// 	// token 
	// 	header.Authorization =  'Bearer ' +  uni.getStorageSync('token');  
	// }
	// header.source=1;
	// header.channel="h5";
	
	// 
	//console.log('uni.getLocale()====>'+uni.getLocale());
	let title = '';
	if(uni.getLocale()=='en'){
		title = 'Loading...'
	}else{
		title = '...'
	}
	uni.showLoading({
		title:title
	});
    // promise
	return new Promise((resolve,reject)=>{  
		// request
		uni.request({
			url, //url
			method:option.method||"GET", //
			header, //
			timeout,
			data:option.data||option.params, //
			success(res){
				//debugger
				// 
				if(res.data.result=='ok'){
					resolve(res.data.data)
				} else{
					reject(res)
				}
				uni.hideLoading()
			},
			fail(err){
				uni.hideLoading()
				// 
				console.error(err);
				let title_ = '';
				let content_ = '';
				if(uni.getLocale()=='en'){
					title_ = 'Tips',
					content_ = 'System exception, please try again later'
				}else{
					title_ = ''
					content_ = ','
				}
				uni.showToast({
					title:title_,
					title:content_,
					icon:'none'
				});
			},
			complete(){
				//  loading
				uni.hideLoading()
			}
		})
	})
}

// get
request.get=function(url,data,config){
	return  request({
		url,
		method:"GET",
		...config,
		data
	})
}

// post
request.post=function(url,data,config){
	return  request({
		url,
		method:"POST",		
		...config,
		data
	})
}
// 
export default request;
